// Typography
$base-font-family: "clarendon-text-pro", "Clarendon Text Pro", "Superclarendon", $helvetica;
$heading-font-family: "hwt-artz", "HWT Artz", $base-font-family;

// Font Sizes
$base-font-size: 1em;

// Line height
$base-line-height: 1.5;
$heading-line-height: 1.2;

// Other Sizes
$base-border-radius: 3px;
$base-spacing: $base-line-height * 1em;
$small-spacing: $base-spacing / 2;
$base-z-index: 0;

// Colors
$blue: #477dca;
$dark-gray: #333;
$medium-gray: #999;
$light-gray: #ddd;
$hot-pink: #cc178b;
$light-tan: #edece5;
$hot-yellow: #f8ca43;
$cool-green: #14851a;
$sweet-blue: #488fe2;
$mean-magenta: #e72859;

// Font Colors
$base-background-color: #fff;
$base-font-color: $dark-gray;
$action-color: $blue;

// Border
$base-border-color: $light-gray;
$base-border: 1px solid $base-border-color;

// Forms
$form-box-shadow: inset 0 1px 3px rgba(#000, 0.06);
$form-box-shadow-focus: $form-box-shadow, 0 0 5px adjust-color($action-color, $lightness: -5%, $alpha: -0.3);
